.before-after-image {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.before-after-image >img{
  height: 20vh;
  margin: 7px;
}
  .before, .after {
    position: absolute;
    top: 0;
    left: 0;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s linear, opacity 0.5s linear;
    width: 100%;
    height: auto;
  }
  
  .show {
    display: block;
    height: 100%;
  }
  
  .hide {
    display: none;
  }